<template>
  <Alert :arrayAlert="arrayAlert" @changeShow="closeAlert($event)" />
  <div id="PersonsTable">
    <Table
      :tableProps="tableProps"
      @deleteItem="deletePerson"
      @updateItem="updatePerson"
    />
    <div class="bottom-table">
      <div class="table-person-add">
        <button
          class="btn-form blue"
          @click="addNewPerson($event)"
          v-bind:data-title="getLocalizationtxt.personPage.create"
        >
          {{ getLocalizationtxt.personPage.add_person }}
        </button>
      </div>
    </div>
  </div>
  <Popap :PopapProps="PopapProps" v-on:closePopap="closePopap">
    <template #headerPopap>
      <div class="header">
        <h3>{{ popapTitle }}</h3>
      </div>
    </template>
    <template #bodyPopap
      ><div class="body">
        <div
          v-if="popapType === 'update' || popapType === 'create'"
          class="form-input"
          @keydown.enter="saveForm"
        >
          <div class="row-input">
            <input
              type="text"
              v-model="checkedPersonInfo.name"
              @input="
                handlingFormInput($event, 'string', 'name', 'errorName', 2)
              "
              v-bind:placeholder="getLocalizationtxt.userPage.name"
              @paste="eventStop"
              class="input-form"
              ref="inputName"
              maxlength="16"
            />
            <p :class="checkedPersonInfo.errorName" class="error-input name">
              {{ getLocalizationtxt.userPage.nameError }}
            </p>
          </div>
          <div class="row-input">
            <input
              type="text"
              v-model="checkedPersonInfo.surname"
              @input="
                handlingFormInput(
                  $event,
                  'string',
                  'surname',
                  'errorSurname',
                  2
                )
              "
              v-bind:placeholder="getLocalizationtxt.userPage.surname"
              @paste="eventStop"
              class="input-form"
              ref="inputSurname"
              maxlength="16"
            />
            <p
              :class="checkedPersonInfo.errorSurname"
              class="error-input surname"
            >
              {{ getLocalizationtxt.userPage.surnameError }}
            </p>
          </div>
          <div class="row-input mb-0">
            <input
              type="text"
              v-model="checkedPersonInfo.phone"
              @input="
                handlingFormInput($event, 'number', 'phone', 'errorPhone', 6)
              "
              maxlength="16"
              v-bind:placeholder="getLocalizationtxt.userPage.phone"
              @paste="eventStop"
              class="input-form"
              ref="inputPhone"
            />
            <p :class="checkedPersonInfo.errorPhone" class="error-input phone">
              {{ getLocalizationtxt.userPage.phoneError }}
            </p>
          </div>
        </div>
        <div v-else-if="popapType === 'delete'">
          <p>{{ getLocalizationtxt.personPage.delete_text_confirm }}</p>
        </div>
      </div></template
    >
    <template #footerPopap>
      <div class="footer">
        <button
          class="btn-form blue"
          @click="saveForm"
          data-type=""
          ref="saveForm"
        ></button>
        <button class="btn-form grey ml-1" @click="closePopap">
          {{ getLocalizationtxt.personPage.close }}
        </button>
      </div>
    </template>
  </Popap>
</template>

<script>
import Alert from "@/components/Alert";
import Popap from "@/components/Popap";
import Table from "@/components/Table/Table";
import { emptyValidation, replaceValue } from "@/plugins/Validation";

export default {
  name: "PersonsTable",
  data() {
    return {
      sortButtons: {
        id: "down",
        name: "off",
        surname: "off",
        phone: "off",
      },
      popapTitle: "",
      popapType: "",
      PopapProps: {
        show: false,
      },
      tableProps: {
        header: [
          {
            name: "#",
            prop: "id",
            sort: true,
          },
          {
            name: "",
            prop: "name",
            sort: true,
          },
          {
            name: "",
            prop: "surname",
            sort: true,
          },
          {
            name: "",
            prop: "phone",
            sort: true,
          },
          {
            name: "",
            prop: "",
            sort: false,
          },
        ],
        body: [],
        footer: {
          tableTitleKey: "personPage",
          tableTitleValue: "title_table",
          search: true,
          updateDelete: true,
          show: true,
          updatePagination: false,
        },
      },
      checkedPersonInfo: {
        checkedID: null,
        name: "",
        errorName: "",
        surname: "",
        errorSurname: "",
        phone: "",
        errorPhone: "",
      },
      arrayAlert: {
        msg: "",
        msgType: "",
        show: false,
      },
    };
  },
  components: { Alert, Popap, Table },
  watch: {
    allPersons() {
      this.checkTableItem();
    },
  },
  computed: {
    localizationSelected() {
      return this.$store.getters["getLocalizationSelected"];
    },
    getLocalizationtxt() {
      return this.$store.getters["localizationInfo"];
    },
    allPersons() {
      return this.$store.getters["allPersons"];
    },
  },
  mounted() {
    this.checkTableItem();
  },
  methods: {
    checkTableItem() {
      this.tableProps.header[1].name = this.getLocalizationtxt.userPage.name;
      this.tableProps.header[2].name = this.getLocalizationtxt.userPage.surname;
      this.tableProps.header[3].name = this.getLocalizationtxt.userPage.phone;

      this.tableProps.body = this.allPersons;
    },
    handlingFormInput(event, typeValidation, vModel, errorName, minLength) {
      let value = event.target.value;
      if (typeValidation === "string" || typeValidation === "number") {
        let replacedValue = replaceValue(value, typeValidation);
        value = replacedValue;
        this.checkedPersonInfo[vModel] = replacedValue;
        let errorCheckedEmpty = emptyValidation(value, minLength);
        if (errorCheckedEmpty) this.checkedPersonInfo[errorName] = "true";
        else this.checkedPersonInfo[errorName] = "false";
      }
    },
    addNewPerson() {
      this.emptyPopapForm();
      this.nullPopapErrors();
      this.$refs.saveForm.innerHTML = this.getLocalizationtxt.personPage.create;
      this.$refs.saveForm.classList.remove("danger");
      this.$refs.saveForm.classList.add("blue");
      this.popapTitle = this.getLocalizationtxt.personPage.create;
      this.popapType = "create";
      this.PopapProps.show = true;
      let self = this;
      setTimeout(function () {
        self.$refs.inputName.focus();
      }, 100);
    },
    updatePerson(id) {
      this.popapType = "update";
      this.popapTitle = this.getLocalizationtxt.personPage.update;
      this.$refs.saveForm.innerHTML = this.getLocalizationtxt.personPage.update;
      this.$refs.saveForm.classList.remove("danger");
      this.$refs.saveForm.classList.add("blue");
      this.PopapProps.show = true;
      this.checkedPersonInfo.checkedID = id;
      let checkedPersonInfo = this.$store.getters.getPersonByID(id);
      this.checkedPersonInfo.name = checkedPersonInfo.name;
      this.checkedPersonInfo.surname = checkedPersonInfo.surname;
      this.checkedPersonInfo.phone = checkedPersonInfo.phone;
      this.removePoapErrors();
      let self = this;
      setTimeout(function () {
        self.$refs.inputName.focus();
      }, 100);
    },
    emptyPopapForm() {
      this.checkedPersonInfo.name = "";
      this.checkedPersonInfo.surname = "";
      this.checkedPersonInfo.phone = "";
    },
    nullPopapErrors() {
      this.checkedPersonInfo.errorName = "";
      this.checkedPersonInfo.errorSurname = "";
      this.checkedPersonInfo.errorPhone = "";
    },
    removePoapErrors() {
      this.checkedPersonInfo.errorName = "true";
      this.checkedPersonInfo.errorSurname = "true";
      this.checkedPersonInfo.errorPhone = "true";
    },
    saveForm() {
      const saveType = this.popapType;
      if (saveType === "create" || saveType === "update") {
        if (
          this.checkedPersonInfo.errorName === "true" &&
          this.checkedPersonInfo.errorSurname === "true" &&
          this.checkedPersonInfo.errorPhone === "true"
        ) {
          if (saveType === "create") {
            let data = {
              id: null,
              name: this.$refs.inputName.value,
              surname: this.$refs.inputSurname.value,
              phone: this.$refs.inputPhone.value,
            };
            this.$store.dispatch("A_ADD_NEW_PERSON", data);
            this.closePopap();
            this.arrayAlert.show = true;
            this.arrayAlert.msg =
              this.getLocalizationtxt.alertPage.success_added;
            this.arrayAlert.msgType = "success";
            this.tableProps.footer.updatePagination = true;
          } else if (saveType === "update") {
            let data = {
              id: this.checkedPersonInfo.checkedID,
              name: this.$refs.inputName.value,
              surname: this.$refs.inputSurname.value,
              phone: this.$refs.inputPhone.value,
            };
            this.$store.dispatch("A_UPDATE_PERSON", data);
            this.closePopap();
            this.arrayAlert.show = true;
            this.arrayAlert.msg =
              this.getLocalizationtxt.alertPage.success_updated;
            this.arrayAlert.msgType = "success";
          }
          this.emptyPopapForm();
        } else {
          this.arrayAlert.show = true;
          this.arrayAlert.msg = this.getLocalizationtxt.alertPage.empty_info;
          this.arrayAlert.msgType = "error";
        }
      } else if (saveType === "delete") {
        const send = {
          PersonId: this.checkedPersonInfo.checkedID,
        };
        this.$store.dispatch("A_DELETE_PERSON", send);
        this.arrayAlert.show = true;
        this.arrayAlert.msg = this.getLocalizationtxt.alertPage.success_deleted;
        this.arrayAlert.msgType = "success";
        this.closePopap();
      }
    },
    deletePerson(PersonId) {
      if (PersonId != "") {
        this.popapTitle = this.getLocalizationtxt.personPage.delete;
        this.$refs.saveForm.innerHTML =
          this.getLocalizationtxt.personPage.delete;
        this.$refs.saveForm.classList.remove("blue");
        this.$refs.saveForm.classList.add("danger");
        this.popapType = "delete";
        this.checkedPersonInfo.checkedID = PersonId;
        this.PopapProps.show = true;
      }
    },
    eventStop(event) {
      event.preventDefault();
    },
    closeAlert(data) {
      this.arrayAlert.show = data;
      this.arrayAlert.msg = "";
    },
    closePopap() {
      this.PopapProps.show = false;
    },
  },
};
</script>

<style lang="scss">
.bottom-table {
  width: 80%;
  margin: 10px auto 0px;
  display: flex;
  justify-content: end;
}

#Popap {
  .body {
    border: 1px solid #4a5156;
    border-left: none;
    border-right: none;
    padding: 20px 0;
    margin: 10px 0;

    .form-input {
      display: flex;
      flex-direction: column;
    }
  }

  .footer {
    display: flex;
    justify-content: end;
  }
}

@media only screen and (max-width: 425px) {
  .bottom-table {
    width: 95%;
  }
}
</style>
