export const sortArray = (array, sortOptions) => {
  let sortedArray = [];
  if (sortOptions.prop === "id") {
    sortedArray = array.sort((a, b) => a.id - b.id);
  } else {
    sortedArray = array.sort((a, b) => {
      return a[sortOptions.prop].toLowerCase() >
        b[sortOptions.prop].toLowerCase()
        ? 1
        : a[sortOptions.prop].toLowerCase() < b[sortOptions.prop].toLowerCase()
        ? -1
        : 0;
    });
  }
  return sortOptions.to === "up" ? sortedArray : sortedArray.reverse();
};

export const sliceArray = (array, sliceOptions) => {
  let slicedArray = array.slice(
    (sliceOptions.page - 1) * sliceOptions.pageSize,
    sliceOptions.page * sliceOptions.pageSize
  );
  return slicedArray;
};
