<template>
  <div class="table">
    <div class="table-header">
      <h1 class="table-title">
        {{
          getLocalizationtxt[tableProps.footer.tableTitleKey][
            tableProps.footer.tableTitleValue
          ]
        }}
      </h1>
      <div class="table-settings">
        <div
          class="d-flex align-items-center justify-content-end table-setting-search"
          v-if="tableProps.footer.search"
        >
          <span class="title-setting mr-1"
            >{{ getLocalizationtxt.personPage.search }} :
          </span>
          <input
            type="search"
            class="input-form"
            v-model="searchKeyword"
            v-bind:placeholder="getLocalizationtxt.personPage.search"
            @keyup="toFirstPage('true')"
          />
        </div>
      </div>
    </div>
    <div class="table-body">
      <div class="myTable">
        <div class="tr top">
          <div
            class="th d-flex align-items-center"
            v-for="(item, key) in tableProps.header"
            :key="key"
            :data-id="sortButtons[item.prop]"
            @click="item.sort ? sortByItem($event, item.prop) : null"
          >
            <span v-if="item.prop === 'id'">#</span>
            <span v-else>{{ getLocalizationtxt.userPage[item.prop] }}</span>
            <div
              v-if="item.sort"
              class="content-sort-icon"
              :data-id="sortButtons[item.prop]"
            >
              <img
                src="@/assets/icons/arrow.svg"
                :class="[sortButtons[item.prop], 'arrow-icon-sort']"
              />
            </div>
          </div>
        </div>
        <div class="tr" v-for="(item, key) in sliceItemsByShow" :key="key">
          <div
            class="td"
            v-for="(item2, key2) in tableProps.header"
            :key="key2"
          >
            {{ item[item2.prop] }}
          </div>
          <div
            class="td d-flex justify-content-end"
            v-if="tableProps.footer.updateDelete"
          >
            <img
              class="btn-update mr-1"
              v-bind:data-title="getLocalizationtxt.personPage.update"
              @click="updateItem(item.id)"
              src="@/assets/icons/update.svg"
            />
            <img
              class="btn-delete"
              @click="deleteItem(item.id)"
              src="@/assets/icons/delete.svg"
            />
          </div>
        </div>
      </div>
    </div>
    <div class="table-footer">
      <div
        class="row-pagination-show d-flex align-items-center justify-content-end"
      >
        <div class="d-flex table-pagination-buttons mr-3">
          <button
            class="btn-form disabled btn-pagination mr-1"
            data-active=""
            data-name="back"
            ref="btnPaginationBack"
            @click="toPaginationTable"
          >
            <img
              src="@/assets/icons/arrow.svg"
              class="img-arrow-pagination img-left-arrow-pagination"
            />
          </button>
          <button
            v-for="(value, key) in paginationPageButtons"
            :key="key"
            class="btn-form ml-1 mr-1"
            :class="[paginationOptions.page === value ? 'disabled' : 'grey']"
            @click="changePaginationPage(value)"
          >
            {{ value }}
          </button>
          <button
            class="btn-form disabled btn-pagination ml-1"
            data-active=""
            data-name="next"
            ref="btnPaginationNext"
            @click="toPaginationTable"
          >
            <img
              src="@/assets/icons/arrow.svg"
              class="img-arrow-pagination img-right-arrow-pagination"
            />
          </button>
        </div>
        <div
          class="table-setting-box table-setting-show mr-5"
          v-if="tableProps.footer.show"
        >
          <span class="title-setting"
            >{{ getLocalizationtxt.personPage.show_table }} :
          </span>
          <input
            v-model="paginationOptions.pageSize"
            @input="changeShowTable"
            type="number"
            min="1"
            :max="itemsLength"
            class="input-form"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { sortArray, sliceArray } from "@/plugins/ArrayFunctions";

export default {
  name: "Table",
  data() {
    return {
      searchKeyword: "",
      paginationOptions: {
        page: 1,
        pageSize: 5,
        pageMaxLength: 0,
        mobile: false,
      },
      sortButtons: {
        id: "off",
        name: "off",
        surname: "off",
        phone: "off",
      },
      sortOptions: {
        prop: "id",
        to: "up",
      },
    };
  },
  props: {
    tableProps: {
      type: Object,
      required: true,
    },
  },
  mounted() {
    this.getQueryParams();
    window.addEventListener("resize", this.windowResize);
    this.windowResize();
  },
  computed: {
    itemsLength() {
      if (this.searchedItems.length === 0) {
        return 1;
      } else {
        return this.searchedItems.length;
      }
    },
    getLocalizationtxt() {
      return this.$store.getters["localizationInfo"];
    },
    sortedItems() {
      const sortedArrayItems = sortArray(
        this.tableProps.body,
        this.sortOptions
      );
      this.updateQuery();
      return sortedArrayItems;
    },
    searchedItems() {
      const searchedArray = this.sortedItems.filter((item) => {
        return Object.keys(item).some((key) => {
          return item[key]
            .toString()
            .toLowerCase()
            .includes(this.searchKeyword.toLowerCase());
        });
      });
      this.updateQuery();
      return searchedArray;
    },
    sliceItemsByShow() {
      const slicedArray = sliceArray(
        this.searchedItems,
        this.paginationOptions
      );
      return slicedArray;
    },
    paginationPageButtons() {
      let arrayPageButtons = [];
      let prevPage = +this.paginationOptions.page - 1;
      let nextPage = +this.paginationOptions.page + 1;
      if (this.paginationOptions.pageMaxLength > 6) {
        if (this.paginationOptions.page > "1") {
          arrayPageButtons.push("1");
          if (!this.paginationOptions.mobile) {
            if (this.paginationOptions.page > "3") {
              arrayPageButtons.push("...");
            }
          }
          if (this.paginationOptions.page > "2") {
            arrayPageButtons.push(prevPage);
          }
        }
        arrayPageButtons.push(this.paginationOptions.page);
        if (
          this.paginationOptions.page <
          this.paginationOptions.pageMaxLength - 1
        ) {
          arrayPageButtons.push(nextPage);
        }
        if (!this.paginationOptions.mobile) {
          if (
            this.paginationOptions.page <
            this.paginationOptions.pageMaxLength - 2
          ) {
            arrayPageButtons.push("...");
          }
        }
        if (
          this.paginationOptions.page != this.paginationOptions.pageMaxLength
        ) {
          arrayPageButtons.push(this.paginationOptions.pageMaxLength);
        }
      } else {
        arrayPageButtons = this.paginationOptions.pageMaxLength;
      }
      return arrayPageButtons;
    },
  },
  methods: {
    windowResize() {
      let width = window.innerWidth;
      if (width < 425) {
        this.paginationOptions.mobile = true;
      }
    },
    changeShowTable() {
      if (
        this.paginationOptions.pageSize === "" ||
        this.paginationOptions.pageSize > this.itemsLength
      )
        this.paginationOptions.pageSize = 5;
      this.toFirstPage("true");
      this.updatePagination();
    },
    checkpageMaxLength() {
      this.paginationOptions.pageMaxLength = Math.ceil(
        this.searchedItems.length / this.paginationOptions.pageSize
      );
    },
    toPaginationTable(event) {
      const dataActive = event.target.getAttribute("data-active");
      const dataName = event.target.getAttribute("data-name");
      if (dataActive != "") {
        if (dataName === "next")
          this.paginationOptions.page =
            +this.paginationOptions.page + 1 <=
            this.paginationOptions.pageMaxLength
              ? +this.paginationOptions.page + 1
              : this.paginationOptions.page;
        else if (dataName === "back")
          this.paginationOptions.page =
            this.paginationOptions.page > 2
              ? +this.paginationOptions.page - 1
              : 1;
        else this.paginationOptions.page = dataName;
      }

      this.checkPaginationButtonActive();
    },
    toFirstPage(event) {
      if (
        event != "true" &&
        event.target.value != this.paginationOptions.pageSize
      ) {
        this.paginationOptions.pageSize = event.target.value;
        this.paginationOptions.page = 1;
      }
      if (event === "true") {
        this.paginationOptions.page = 1;
      }
      this.updatePagination();
    },
    changePaginationPage(event) {
      if (!isNaN(event)) {
        this.paginationOptions.page = event;
        this.updatePagination();
      }
    },
    checkPaginationButtonActive() {
      if (this.paginationOptions.page == 1 && this.searchedItems.length != 0) {
        if (this.paginationOptions.pageMaxLength == 1) {
          this.changeBtnPaginationBackOff();
          this.changeBtnPaginationNextOff();
        } else {
          this.changeBtnPaginationBackOff();
          this.changeBtnPaginationNextOn();
        }
      } else if (
        this.paginationOptions.page == this.paginationOptions.pageMaxLength
      ) {
        this.changeBtnPaginationBackOn();
        this.changeBtnPaginationNextOff();
      } else if (
        this.searchedItems.length == 0 ||
        this.paginationOptions.pageMaxLength == 0
      ) {
        this.changeBtnPaginationBackOff();
        this.changeBtnPaginationNextOff();
      } else {
        this.changeBtnPaginationBackOn();
        this.changeBtnPaginationNextOn();
      }
    },
    updatePagination() {
      this.checkpageMaxLength();
      this.checkPaginationButtonActive();
    },
    changeBtnPaginationBackOff() {
      this.$refs.btnPaginationBack.setAttribute("data-active", "");
      this.$refs.btnPaginationBack.classList.remove("grey");
      this.$refs.btnPaginationBack.classList.add("disabled");
    },
    changeBtnPaginationBackOn() {
      this.$refs.btnPaginationBack.setAttribute("data-active", "active");
      this.$refs.btnPaginationBack.classList.add("grey");
      this.$refs.btnPaginationBack.classList.remove("disabled");
    },
    changeBtnPaginationNextOff() {
      this.$refs.btnPaginationNext.setAttribute("data-active", "");
      this.$refs.btnPaginationNext.classList.remove("grey");
      this.$refs.btnPaginationNext.classList.add("disabled");
    },
    changeBtnPaginationNextOn() {
      this.$refs.btnPaginationNext.setAttribute("data-active", "active");
      this.$refs.btnPaginationNext.classList.add("grey");
      this.$refs.btnPaginationNext.classList.remove("disabled");
    },
    sortByItem(event, sortKey) {
      let sortTo = event.target.getAttribute("data-id");
      this.sortButtons.id = "off";
      this.sortButtons.name = "off";
      this.sortButtons.surname = "off";
      this.sortButtons.phone = "off";
      if (sortTo === "down") {
        this.sortOptions.prop = sortKey;
        this.sortOptions.to = "up";
        this.sortButtons[sortKey] = "up";
      } else if (sortTo === "up") {
        if (sortKey != "id") {
          this.sortOptions.prop = "id";
          this.sortOptions.to = "up";
          this.sortButtons.id = "up";
        }
      } else if (sortTo === "off") {
        this.sortOptions.prop = sortKey;
        this.sortOptions.to = "down";
        this.sortButtons[sortKey] = "down";
      }
      this.updateQuery();
    },
    getQueryParams() {
      if (
        this.$route.query.sort != undefined &&
        this.$route.query.sortTo != undefined
      ) {
        this.sortOptions.prop = this.$route.query.sort;
        this.sortOptions.to = this.$route.query.sortTo;
        this.sortButtons[this.$route.query.sort] = this.$route.query.sortTo;
      } else {
        this.sortOptions.prop = "id";
        this.sortOptions.to = "up";
        this.sortButtons[this.sortOptions.prop] = this.sortOptions.to;
      }
      if (this.$route.query.search != undefined) {
        this.searchKeyword = this.$route.query.search;
      }
      if (this.$route.query.show != undefined) {
        this.paginationOptions.pageSize = this.$route.query.show;
      }
    },
    updateQuery() {
      const query = {
        show: this.paginationOptions.pageSize,
        sort: this.sortOptions.prop,
        sortTo: this.sortOptions.to,
      };
      if (this.searchKeyword != "") query.search = this.searchKeyword;
      this.$router.push({ query });
    },
    deleteItem(id) {
      this.$emit("deleteItem", id);
      this.paginationOptions.page = 1;
      this.updatePagination();
    },
    updateItem(id) {
      this.$emit("updateItem", id);
    },
  },
  watch: {
    "tableProps.body"() {
      this.updatePagination();
    },
    "tableProps.footer.updatePagination"() {
      this.updatePagination();
    },
  },
};
</script>

<style lang="scss" scoped>
.table {
  width: 80%;
  margin: 0 auto;
  border: solid 1px #ced4da;
  background-color: rgb(255 255 255 / 10%);

  .table-header {
    margin-bottom: 20px;
    padding-top: 15px;

    .table-title {
      text-align: center;
      margin-bottom: 20px;
    }

    .table-settings {
      padding: 0 20px;
    }
  }

  .table-body {
    padding: 10px 20px;

    .myTable {
      min-height: 175px;

      .tr {
        display: grid;
        grid-template-columns: 4% repeat(5, 1fr);
        padding: 5px 0;
      }
      .tr:not(.top):hover {
        background-color: #ced4da;
      }
      .tr.top {
        border-bottom: 2px solid #4a5156;
      }
      .tr.top .th {
        cursor: pointer;
      }
      .tr.top .th:hover .arrow-icon-sort {
        display: block;
      }
      .tr.top .th span {
        pointer-events: none;
      }
      .tr.top .th .arrow-icon-sort {
        width: 10px;
        pointer-events: none;
        display: none;
      }
      .tr.top .th .arrow-icon-sort.off {
        transform: rotateZ(90deg);
      }
      .tr.top .th .arrow-icon-sort.down {
        transform: rotateZ(0deg);
      }
      .tr.top .th .arrow-icon-sort.up {
        transform: rotateZ(180deg);
      }
      .th {
        font-weight: bold;
      }
      .th,
      .td {
        padding: 0 5px;

        .btn-update,
        .btn-delete {
          cursor: pointer;
          width: 15px;
        }
      }

      .table-pagination-buttons {
        display: flex;
        justify-content: flex-end;
        margin-left: 20px;

        .btn-pagination:nth-child(1) {
          margin-right: 5px;
        }
      }
    }
  }

  .table-footer {
    padding: 10px 20px;

    .btn-pagination {
      position: relative;
      padding: 12px 16px;

      .img-arrow-pagination {
        width: 12px;
        position: absolute;
        pointer-events: none;
      }
      .img-left-arrow-pagination {
        transform: translate(-50%, -50%) rotateZ(90deg);
      }
      .img-right-arrow-pagination {
        transform: translate(-50%, -50%) rotateZ(270deg);
      }
    }

    .table-person-add {
      display: flex;
      justify-content: end;
    }
  }
}

@media only screen and (max-width: 425px) {
  .table {
    width: 96%;

    .table-settings {
      display: flex;
      justify-content: center;
    }

    .table-body {
      overflow-x: scroll;

      .myTable {
        width: max-content;
      }
    }
  }
  .row-pagination-show {
    flex-direction: column;
    align-items: flex-end;

    .table-pagination-buttons,
    .table-setting-show {
      margin: 0 0 20px;
    }
  }
}
</style>
